@import '../../common/styles/sass/variables';

.skillsBlock {
  /*min-height: 100vh;*/
  background-color: $bgColorSecond;
  display: flex;
  align-items: stretch;

  .skillsContainer {
    flex-direction: column;
    justify-content: flex-start;

    .skills {
      //border: 1px solid;
      width: 100%;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;

      @media screen and (max-width: 627px) {
        justify-content: center;
      }
    }
  }
}